body {
    font-family: 'Helvetica', 'sans-serif';
    font-size: 16px;
    background-color: #ffffff;
    color : #000000;
    margin: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

header {
    margin: 10px 50px;
}

main {
    height: 100%;
    /* margin: 10px 50px; */
}

.viz-container{
    height: 100%;
}

.graph {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

h1 {
    color: darkmagenta;
}

.chart{
    position: relative;
    padding: 5px;
}

#main-container{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 100%;
}
#page-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
}
#top-bar{
    display: flex;
    flex-direction: row;
    height: 75px;
    background-color: red;
}

#progress-bar {
    display: flex;
    flex-direction: row;
    height: 50px;
    background-color: red;
}

#icon{
    text-align: center;
    height: auto;
    padding: 10px;
}

.w-100 {
    width: 100%;
}

.h-100 {
    height: 100%;
}

.main-wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 10px;
    padding-right: 10px;
}

.middle-panel {
    border-radius: 10px;
    border: 1px 1px black
}

.constructor-option {
    margin-left: 5px;
}

.accent-background {
    background: rgb(255, 251, 236);
}

.regulation-invisible {
    display: none;
}

.regulation-visible {
    display: block
}

.legend {
    margin-block: 20px !important;
}